
import { Cell, CellGroup,Switch } from 'vant'
import Icon from 'vux/src/components/icon/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

import PlaceHolder from 'components/placeholder'
import {getSearch} from 'api'
import {saveSearch} from '../../../static/js/cache.js'  
import storage from 'good-storage'  
export default({
data () {
return {
searchVal: this.$route.params.items || '' ,
currentId: 0,
value:false,
priceSort:3,
saleSort:5,
priceActive: false,
saleActive:false,
goodsList: [],
pagesize: 10,
page: 1,
IslastPage: false,
coupon:false, //默认不带优惠券
sortNo: 0, //默认搜索
clientHeight: 0,
onFetching: false,
scrollHeight: '0px',
isLoading:false
}
},
components: {
Icon,
XSwitch,
Scroller,
LoadMore,
'vanCell':Cell,
'vanCellGroup':CellGroup,
'vanSwitch':Switch,
PlaceHolder,
Loading
},
methods: {
toFixed(val){
return val.toFixed(2)
},
onScrollBottom () {
let that = this
if (this.onFetching || this.IslastPage || that.page >=that.lastPage || that.goodsList.length <= 0) {
return false
} else {
that.onFetching = true
var page =  ++ that.page
let data = {
coupon : that.coupon,
keyword : that.searchVal,
page: page,
sort: that.sortNo
}
getSearch(data).then(res =>{
if(res.code === 0){
let IslastPage = false
if(that.page >= res.result.total_page)
IslastPage = true
that.goodsList = that.goodsList.concat(res.result.data)
that.page = res.result.current_page
that.lastPage = res.result.total_page
that.onFetching = true
that.IslastPage = IslastPage
}
that.onFetching = false
that.$nextTick(() => {
that.$refs.scrollerBottom.reset()
})
})
}
},
fetchList(data){
let that = this
that.isLoading = true
getSearch(data).then(res =>{
if(res.code === 0){
that.goodsList = res.result.data
that.page = res.result.current_page
that.lastPage = res.result.total_page
that.onFetching = false
that.IslastPage = false
}else{
that.goodsList = []
}
that.$nextTick(() =>{
this.$refs.scrollerBottom.reset({top: 0})
})
that.isLoading = false
})
},

searchBtn(e){
let that = this
if(that.searchVal === '') return
let searches=storage.get('_search_') || [];
if( that.searchVal.match(/^\s*$/) == null){
if(searches.indexOf(that.searchVal) === -1){
saveSearch(that.searchVal)
}
}
let data = {
coupon : that.coupon,
keyword : that.searchVal,
page: 1,
sort: 0
}
that.fetchList(data)
},
toDetail(item){
let hasCount = item.item_coupon
console.log(JSON.stringify(hasCount))
sessionStorage.setItem('detailData',JSON.stringify(item))
this.$store.commit('SET_INFOMATION',item)
if(hasCount == undefined){
this.$router.push({path: `/detail/${item.item_id}`});
}else{
this.$router.push({path: `/detail/${item.item_id}/${item.item_coupon.info}`});
}

},

tabclick(e){
let that = this
let currentId = e.target.dataset.current
if(that.searchVal === '') return
this.currentId = currentId
switch (currentId){
case '0':
that.sortNo = 1
break;
case '1':
that.sortNo = 2
break;
default:that.sortNo = 0;
}
let data = {
coupon : that.coupon,
keyword : that.searchVal,
page: 1,
sort: that.sortNo
}
that.fetchList(data)
},

priceCLICK(e){
let that = this
if(that.searchVal === '') return
that.priceActive = !that.priceActive
that.currentId = 2
if(that.priceActive){
that.sortNo = 3
}else{
that.sortNo = 4
}
let data = {
coupon : that.coupon,
keyword : that.searchVal,
page: 1,
sort: that.sortNo
}
that.fetchList(data)
},

saleCLICK(e){
let that = this
if(that.searchVal === '') return
that.saleActive = !that.saleActive
that.currentId = 3
if(that.saleActive){
that.sortNo = 5
}else{
that.sortNo = 6
}
let data = {
coupon : that.coupon,
keyword : that.searchVal,
page: 1,
sort: that.sortNo
}
that.fetchList(data)
},
switchChange(e){
let that = this
that.coupon = e
if(that.searchVal === '') return
let data = {
coupon : that.coupon,
keyword : that.searchVal,
page: 1,
sort: 0
}
that.fetchList(data)
}
},
mounted () {
let that = this
let goodsName = that.$route.params.items
let data = {
coupon : false,
keyword : that.searchVal,
page: 1,
sort: 0
}
that.$nextTick(() =>{
that.clientHeight = `${document.documentElement.clientHeight}px`
that.scrollHeight = String(-that.$refs.searchBox.offsetHeight - that.$refs.searchBox.offsetHeight - that.$refs.switchBox.offsetHeight)
this.$refs.scrollerBottom.reset({top: 0})
if(that.searchVal === '') return
that.fetchList(data)
})
}
})



